/* HEADER */
.navbar {
    .navbar-toggler {
        border-color: rgba(255, 255, 255, 0) !important;
    }
    .navbar-brand {
        font-family: 'Raleway', sans-serif;
    }
    .nav-items {
        border-bottom: 1px solid #00000062;

        li span {
            vertical-align:super;
            font-size: 12px;
        }
    }
    .c-offcanvas-bg.is-open {
        display: none;
    }
    .c-offcanvas--left {
        height: 100vh;
        width: 100%;
        z-index: 10;
        left: 0;
    }
    .arrow-back {
        color: #121212;
    }
}
/* FOOTER */
.footer-contact {
    background: url(.././images/bg-footer.jpg);
    background-size: cover;
    padding: 84px 0;
}
.list-style-none {
    list-style: none;
}